.wrapper {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.inputs-container {
  width: 50%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 1.5rem;
  border: 1px solid #b7c2cc;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
}

.inputs {
  padding: 1rem;
  width: 100%;
  outline: none !important;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  /* border-radius: 2px; */
}

.input-airports:focus::placeholder {
  color: transparent;
}

.inputs-disabled:disabled {
  color: #e0e0e0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.airports-container {
  display: flex;
  background: #fff;
  width: 100%;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  border: 2px solid #ddd;
  border-radius: 6px;
  padding: 2rem 1rem;
  margin-bottom: 0.5rem;
}

.airports-container:focus {
  outline: none !important;
  border: 4px solid #0361e4;
}

.airports-list {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: flex-start;
  width: 100%;
  border-radius: 6px;
}

.airports-list ul {
  width: 100%;
  margin: 0;
  padding: 0;
}

.airports-list li {
  display: flex;
  padding: 0.5rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  width: 100%;
  justify-content: space-between;
  cursor: pointer;
}

.airports-list li:hover {
  background-color: #b7c2cc;
}

.airports-list p {
  width: 50%;
}

.airports-code {
  align-self: flex-end;
}

.search-button {
  width: 30%;
  padding: 0.25rem 1rem;   
  background-color: transparent;
  border-radius: 6px;
  border: 3px solid #0361e4;
  color: #0361e4;
  font-size: 1.3rem;
  font-weight: 600;
  align-self: center;
  cursor: pointer;
  margin: 0.5rem 1rem;
}

.search-button:disabled {
  width: 50%;
  padding: 0.25rem 1rem;   
  background-color: transparent;
  border-radius: 6px;
  border: 3px solid #ff7961;
  color: #ff7961;
  font-size: 1.3rem;
  font-weight: 600;
  cursor: auto;
  align-self: center;
}


svg {
  cursor: pointer;
  color: #0361e4;
  padding: 0.3rem;
}

svg:hover {
  background-color: #0361e4;
  color: white;
  border-radius: 3px;
  padding: 0.3rem;
}

.total-column {
  grid-column: none;
  margin: 1rem 1.2rem;
  font-size: 1.3rem;
  font-weight: bolder;
}

.discount {
  text-decoration: line-through;
}


@media only screen and (max-width: 421px) {
  .inputs-container {
    width: 80%;
    height: 100%;
  }
  .airports-list li:active {
    background-color: none;
  }

  .search-button {
    width: auto;
  }
}